.CogImprove {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
  gap: 20px;
}

.game-card {
  display: flex;
  border: 1px solid #ccc;
  border-radius: 15px;
  padding: 20px;
  background-color: white;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  width: 400px;
}

.game-card:hover {
  transform: translateY(-5px);
}

.game-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  margin-right: 20px;
}

.game-cell {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8bc34a;
  color: white;
  height: 35px;
  border-radius: 5px;
  font-size: 12px;
}

.game-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.game-info h2 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
}

.game-info p {
  margin: 5px 0;
  color: #777;
  font-size: 14px;
}

.game-stats {
  margin: 10px 0;
}

.stat-row {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.stat-input {
  width: 40px;
  margin: 0 5px;
}

.reset-button {
  margin: 10px 0;
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.reset-button:hover {
  background-color: #0056b3;
}

.game-difficulty {
  display: flex;
  align-items: center;
  margin: 10px 0;
  font-size: 14px;
}

.stars {
  color: #ff9800;
  margin-left: 5px;
}

.game-points {
  font-size: 18px;
  font-weight: bold;
  color: #ff5722;
  align-self: flex-end;
}
