.ProductItem {
  width: 100%;
  max-width: 350px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin: 10px 0;
  padding: 20px;
  text-align: center;
  background-color: #fff;
  margin: 0 auto;
}

.ProductHeader {
  font-size: 24px;
  margin-bottom: 10px;
}

.ProductImage {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.ProductInfo {
  margin-bottom: 20px;
}

.InfoBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.InfoBox span {
  font-weight: bold;
}

.InfoBox:nth-child(1) {
  background-color: #ffcccb;
}

.InfoBox:nth-child(2) {
  background-color: #ffdab9;
}

.InfoBox.RemainingPoints {
  background-color: #98fb98;
}

.PurchaseConfirmation {
  font-size: 18px;
  margin-bottom: 20px;
}

.BuyButton {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.BuyButton:hover {
  background-color: #45a049;
}
