.gift-item-container {
  justify-content: center;
  align-items: center;
}

.gift-item {
  width: 352px;
  padding: 10px;
  background: white;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 17px;
  display: flex;
}

.gift-image {
  width: 114px;
  height: 129.27px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.gift-label {
  height: 33px;
  padding-top: 7.73px;
  padding-bottom: 11.27px;
  padding-left: 11px;
  padding-right: 237px;
  background: #ef7347;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  overflow: hidden;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.gift-label-text {
  width: 84px;
  height: 14px;
  color: white;
  font-size: 15px;
  font-family: "Pretendard", sans-serif;
  font-weight: 400;
  word-wrap: break-word;
}

.gift-description {
  height: 122px;
  padding-top: 8px;
  padding-left: 21px;
  padding-right: 12px;
  border-left: 0.5px #b8b8b8 solid;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 11px;
  display: inline-flex;
}

.gift-title {
  width: 91px;
  height: 23px;
  text-align: center;
  color: black;
  font-size: 24px;
  font-family: "Pretendard", sans-serif;
  font-weight: 400;
  word-wrap: break-word;
  white-space: nowrap; /* 텍스트를 한 줄로 표시 */
}

.gift-details {
  width: 152px;
  height: 8px;
  color: black;
  font-size: 14px;
  font-family: "Pretendard", sans-serif;
  font-weight: 400;
  word-wrap: break-word;
}
