.word-game {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.word-game h1 {
  background-color: #ff6f61;
  color: white;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
}

.word-game p {
  margin: 10px 0;
}

.word-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  margin-bottom: 20px;
}

.word-cell {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8bc34a;
  color: white;
  height: 50px;
  border-radius: 5px;
  font-size: 16px;
}

.input-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

.input-row {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.input-box {
  width: 50px;
  margin: 0 5px;
}

.submit-button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #0056b3;
}
