.barcode-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.info-text {
  font-size: 14px;
  color: #777;
  margin-bottom: 20px;
}

.product-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  background-color: #fff;
}

.share-button {
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.share-button::before {
  content: "공유하기";
  margin-right: 5px;
}

.product-image {
  width: 390px;
  height: auto;
  margin-bottom: 10px;
}

.product-name {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.product-details {
  font-size: 14px;
  color: #777;
  margin-bottom: 5px;
}
