.ShopWrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 20px;
}

.ShopContainer {
  width: 440px;
  height: 844px;
  overflow: hidden;
  border: 1px solid #ccc; /* 원하는 테두리 스타일을 추가할 수 있습니다 */
  position: relative;
  padding: 20px;
  align-items: center;
}

.ShopContent {
  height: calc(100% - 120px);
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}
